* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

header {
  z-index: 100;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa-arrow-left {
  padding-right: 1rem;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.signout-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover {
  color: #6b2da8;
}

.signout-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

.section-contain {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 100vh;
  padding: 5rem 12%;
  display: flex;
}

.qr-details {
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: 1rem;
  width: 70%;
  height: 100%;
}

#reader {
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 100%;
  display: flex;
}

.detection-status {
  padding: 1rem;
}

.hidden {
  display: none;
}

.fullname {
  gap: .5rem;
  display: flex;
}

.section-contain p, .section-contain h1 {
  color: #fff;
}

.scanning-effect {
  align-items: center;
  height: 2rem;
  display: flex;
}

.typed-text {
  color: #fff;
  font-size: 2rem;
}

@media (width <= 480px) {
  section {
    height: auto;
  }

  .fa-arrow-left {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .section-contain {
    flex-direction: column;
    height: auto;
    display: flex;
  }

  .qr-details {
    flex-direction: column;
    gap: .5rem;
    width: 100%;
    display: flex;
  }

  #reader {
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
  }
}
